body {
    background: linear-gradient(135deg, #2a0059, #5b0089);
    color: white;
    font-family: 'Poppins', 'Arial', sans-serif;
    text-align: center;
    
}

.Main {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    max-width: 1600px; 
    margin: 0 auto; 
    padding: 20px;
    gap: 20px; 
}

.SearchResults, .Playlist {
    width: 45%;
    background: rgba(0, 0, 0, 0.3);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    padding: 20px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.toBeSavedPlaylist, .savedPlaylist {
    width: 100%;
    background: rgba(0, 0, 0, 0.5);  
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.SearchResults > *, .Playlist > * {
    width: 95%;
}

@media (max-width: 768px) {
    .SearchResults, .Playlist {
        width: 100%;
    }

    .Main {
        flex-direction: column; 
        align-items: center; 
}
}

h1 {
    font-size: 3rem;
    text-transform: uppercase;
    font-weight: bold;
    color: #ffffff;
    text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.8);
}

.container {
    width: 80%;
    margin: auto;
    padding: 2rem;
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2);
}

input {
    width: 60%;
    padding: 12px;
    border: none;
    border-radius: 10px;
    font-size: 1.2rem;
    text-align: center;
    background: rgba(255, 255, 255, 0.2);
    color: white;
    outline: none;
}

input::placeholder {
    color: rgba(255, 255, 255, 0.7);
    text-align: center;
    width: 100%;
}

button {
    margin-bottom: 1rem;
    padding: 12px 24px;
    font-size: 1.2rem;
    font-weight: bold;
    text-transform: uppercase;
    color: white;
    background: linear-gradient(135deg, #ff00ff, #8a2be2);
    border: none;
    border-radius: 50px;
    cursor: pointer;
    box-shadow: 0px 0px 15px rgba(255, 0, 255, 0.5);
    transition: background-color 0.3s, transform 0.1s ease-in-out;
}

button:hover {
    background: linear-gradient(135deg, #8a2be2, #ff00ff);
}

button:active {
    background: linear-gradient(135deg, #6a1b9a, #6a00b3);
}

.unselectButton {
    margin-bottom: 1rem;
    padding: 12px 24px;
    font-size: 1.1rem;
    font-weight: bold;
    text-transform: uppercase;
    color: white;
    background: linear-gradient(135deg, #888888, #555555);
    border: none;
    border-radius: 50px;
    cursor: pointer;
    box-shadow: 0px 0px 10px rgba(128, 128, 255, 0.5);
    transition: background-color 0.3s, transform 0.1s ease-in-out;
}

.unselectButton:hover {
    background: linear-gradient(135deg, #777777, #444444);
    transform: scale(1.05);
}

.Playlist ul {
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Playlist li {
    font-size: 1rem;
    background: rgba(255, 255, 255, 0.1);
    padding: 10px;
    margin: 10px 0;
    border-radius: 10px;
    text-align: center;
    width: 100%;
    color: white;
}

.Tracklist {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.track {
    background: rgba(255, 255, 255, 0.1);
    padding: auto;
    margin: 10px 0;
    border-radius: 10px;
    text-align: center;
    width: 100%;
    color: white;
}

